/**
 * BASE
 */

html,
body {
	height: 100%;
	width: 100%;
}

html {
	box-sizing: border-box;
	font-size: 62.5%;
	letter-spacing: 0.05em;
}

body {
	margin: 0;
	counter-reset: a;
	font-family: "Cabin", sans-serif;
}

h1,
.h1 {
	font-family: "NovecentoWide", sans-serif;
	font-size: 4rem;
}

h2,
.h2 {
	font-family: "NovecentoWide", sans-serif;
	font-size: 3rem;
}

h3,
.h3 {
	font-size: 2.5rem;
	letter-spacing: 0.1em;
}

h4,
.h4 {
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.6rem;
	letter-spacing: 0.05em;
}

*,
*::after,
*::before {
	box-sizing: inherit;
}

a {
	color: currentColor;
	cursor: pointer;
	text-decoration: none;
}

