}

.display-block {
	display: block;
}

.valign-middle {
	vertical-align: middle;
}

.overflow-hidden {
	overflow: hidden;
}

.visually-hidden {
	visibility: hidden;
}

.opacity-none {
	opacity: 0;
}

.fluid {
	display: block;
	width: 100%;
	height: auto;
}

.clearfix::after,
.clearfix::before {
	content: "";
	display: table;
}

.clearfix::after {
	clear: both;
}

.float-left {
	float: left;
}

.float-right {
	float: right;
}

.position-fixed {
	position: fixed;
}

.position-relative {
	position: relative;
}

.position-absolute {
	position: absolute;
}

.margin-center {
	margin: 0 auto;
}

.background-white {
	background-color: #fff;
}

.border-white {
	border-color: #fff;
}

.background-cover {
	background-size: cover;
	background-position: center;
}

.background-dark-grey {
	background-color: #363334;
}

.background-light-grey {
	background-color: #f3f3f3;
}

.menu-inline {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.menu-inline li {
	display: inline-block;
}

.embed-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;
}

.embed-container > iframe,
.embed-container > object,
.embed-container > embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

/**
 * COMPONENTS
 */

.home .menu-item {
	color: #fff;
}

.home .site-header {
	height: 76.4rem;
	background-attachment: fixed;
	background-image: url("../../dist/img/min//ecoflam__home.jpg");
	background-position: center;
	background-size: cover;
}

.home .site-header__logo .svg .change-fill {
	fill: #fff;
}

.site-footer .svg .change-fill {
	fill: #fff;
}

.menu-item.current-menu-item > a {
	border: 1px solid currentColor;
}

.menu-item > a {
	padding: 0.5rem 1rem;
	border: 1px solid transparent;
	border-radius: 4px;
	color: currentColor;
	font-size: 1.4rem;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	transition: 0.3s ease;
}

.menu-item > a:hover {
	border: 1px solid currentColor;
	transition: 0.3s ease;
}

/**
 * COMPONENTS: ANIMATIONS
 */

.grayscale {
	filter: grayscale(100%);
	transition: 0.3s ease;
}

.grayscale:hover {
	filter: grayscale(0);
	transition: 0.3s ease;
}

.hoverable {
	position: relative;
}

.hoverable::after {
	display: block;
	height: 2px;
	width: 0;
	position: absolute;
	bottom: -7px;
	left: auto;
	right: 0;
	background-color: #c13b27;
	border-radius: 1px;
	content: "";
	transition: width .3s ease;
}

.hoverable:hover::after {
	width: 100%;
	left: 0;
	right: auto;
}

/**
 * LAYOUT
 */

.inner {
	height: 100%;
	width: 100%;
	margin: 0 auto;
	max-width: 1024px;
}

.inner-wrapper {
	margin: 0 35px;
}

.inner-wrapper .list-multiple-column {
	padding-left: 2rem;
	break-inside: column;
	column-count: 3;
	column-gap: 4rem;
}

.inner-wrapper .text-multiple-column {
	break-inside: column;
	column-count: 2;
	column-gap: 2rem;
}

.inner-wrapper .text-multiple-column > p {
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
}

.inner.has-padding {
	padding-bottom: 14rem;
}

.inner.is-last {
	padding-bottom: 28.5rem;
}

.oblic-rtl {
	position: relative;
}

.oblic-rtl::before {
	display: block;
	position: absolute;
	top: -144px;
	height: 144px;
	width: 100%;
	background-color: inherit;
	content: "";
	clip-path: polygon(0% 100%, 100% 0%, 100% 100%);
}

.oblic-ltr {
	position: relative;
}

.oblic-ltr::before {
	display: block;
	position: absolute;
	top: -144px;
	height: 144px;
	width: 100%;
	background-color: inherit;
	content: "";
	clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
}

.site-section__title.has-no-count {
	margin-top: 9rem;
}

.site-section__title.has-no-count::before {
	display: none;
}

.archive-wrapper,
.page-wrapper {
	background-image: url("../../img//svg/ecoflam__background-page.svg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: 100% auto;
}

.home .page-wrapper {
	background: none;
}

.site-section__title {
	position: relative;
	margin-bottom: 6rem;
	margin-top: 0;
	letter-spacing: 0.3em;
	text-transform: uppercase;
}

.site-section__title .svg {
	max-width: 100%;
	height: auto;
}

.site-section__title::after {
	display: block;
	height: 0.4rem;
	width: 8.5rem;
	position: absolute;
	left: 50%;
	bottom: -6rem;
	background-color: #c13b27;
	border-radius: 0.2rem;
	content: "";
	transform: translate(-50%);
}

.site-section__title::before {
	display: block;
	color: #7c7a7b;
	content: counter(a, decimal-leading-zero);
	counter-increment: a 1;
	font-family: "Cabin", sans-serif;
	letter-spacing: 0.05em;
	margin-bottom: 3rem;
	margin-top: 1rem;
}

.site-section__text:first-child() {
	text-align: right;
}

.site-section.entreprise .entreprise__description {
	margin: 1.7rem 0 0 0;
	color: #363334;
	font-size: 1.5rem;
}

.site-section .text-20 {
	font-size: 2rem;
	line-height: 2.6rem;
}

.site-section .text-20 .color-red {
	color: #c13b27;
}

.site-section .text-16 {
	font-size: 1.6rem;
	line-height: 2.6rem;
}

.site-section .text-16 .color-red {
	color: #c13b27;
}

.insert {
	position: absolute;
	left: 0;
	right: 0;
	top: -200px;
}

.insert__inner {
	background-color: #fff;
	border: 1px solid #363334;
	border-radius: 4px;
}

.insert__title {
	position: absolute;
	left: 50%;
	top: 0;
	padding: 1.2rem;
	background-color: #363334;
	border-radius: 4px;
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.8rem;
	letter-spacing: 0.1em;
	transform: translate(-50%, -50%);
}

.insert__content {
	margin: 4rem 0 1.7rem 0;
}

.insert__content__list {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.insert__content__list__item {
	margin: 0.7rem 0;
	font-size: 1.6rem;
}

.insert__content__list__item::before {
	display: inline-block;
	color: #c13b27;
	content: "- ";
}

.insert__content__list__item::after {
	display: inline-block;
	color: #c13b27;
	content: " -";
}

.products__title {
	margin: 0;
	font-family: "NovecentoWide", sans-serif;
	letter-spacing: 0.3em;
}

.products__link {
	display: inline-block;
	margin: 2rem 0 7rem 0;
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.6rem;
}

.products__img {
	margin-bottom: 3rem;
	max-width: 100%;
	height: auto;
}

.brands {
	margin: 1.5rem 0;
}

.event {
	position: relative;
}

.event-nav {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
}

.event-buttonPrevious {
	height: 4rem;
	width: 4rem;
	margin: 0 0 0 0.5rem;
	background-color: transparent;
	border: 1px solid currentColor;
	color: #7c7a7b;
	line-height: 1;
	text-align: center;
	transition: 0.3s ease;
}

.event-buttonPrevious:hover {
	border-color: currentColor;
	color: #c13b27;
	transition: 0.3s ease;
}

.event-buttonPrevious:hover > .svg {
	transition: 0.3s ease;
}

.event-buttonPrevious:hover > .svg .change-fill {
	fill: #c13b27;
	transition: 0.3s ease;
}

.event-buttonNext {
	height: 4rem;
	width: 4rem;
	margin: 0 0 0 0.5rem;
	background-color: transparent;
	border: 1px solid currentColor;
	color: #7c7a7b;
	line-height: 1;
	text-align: center;
	transition: 0.3s ease;
}

.event-buttonNext:hover {
	border-color: currentColor;
	color: #c13b27;
	transition: 0.3s ease;
}

.event-buttonNext:hover > .svg .change-fill {
	fill: #c13b27;
	transition: 0.3s ease;
}

.event-list {
	position: relative;
	overflow: hidden;
}

.event-item {
	position: absolute;
	display: inline-block;
	width: 100%;
	left: 0;
	top: 0;
	visibility: hidden;
}

.event-item__date {
	margin: 0;
	color: #7c7a7b;
	text-transform: uppercase;
}

.event-item__place {
	margin: 0;
	text-transform: uppercase;
	letter-spacing: 0.2em;
}

.event-item__description {
	margin: 3.5rem 0 0 0;
	color: #fff;
	font-size: 1.6rem;
	line-height: 2.6rem;
}

.current {
	visibility: visible;
	position: relative;
}

.event--slide .event-item.current {
	visibility: visible;
	position: relative;
}

.event--slide .event-item--showPrevious {
	animation: slideFromLeft 350ms 10ms ease-in-out both;
}

.event--slide .event-item--showNext {
	animation: slideFromRight 350ms 10ms ease-in-out both;
}

.event--slide .event-item--hidePrevious,
.event--slide .event-item--hideNext {
	visibility: visible;
}

.event--slide .event-item--hidePrevious {
	animation: slideToLeft 350ms 10ms ease-in-out both;
}

.event--slide .event-item--hideNext {
	animation: slideToRight 350ms 10ms ease-in-out both;
}

/**
 * HEADER
 */

.menu--is-open .site-header__nav {
	transform: translate3d(0, 0, 0);
	opacity: 1;
}

.menu--is-open .site-header__nav__overlay {
	display: block;
	opacity: 1;
}

.site-header {
	position: relative;
}

.site-header .inner {
	padding-top: 4rem;
}

.site-header__logo .svg {
	max-width: 100%;
	height: auto;
}

.site-header__nav__button-open {
	height: 3rem;
	padding: 0 0.5rem;
	border: none;
	border-radius: 4px;
	color: #fff;
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.8rem;
	letter-spacing: 0.1;
	background: #363334; /* The Fallback */
	background: rgba(54, 51, 52, 0.5);
}

.site-header__nav__button-open::after {
	display: inline-block;
	margin: 0 0 0 1rem;
	height: 1.6rem;
	width: 2rem;
	content: "";
	background-image: url("../../img//svg/ecoflam__menu-responsive.svg");
	background-size: auto 100%;
	display: none;
}

.site-header__nav__button-close {
	padding: 0 2.5rem;
	width: 100%;
	position: absolute;
	left: 0;
	right: 0;
	top: 4rem;
	background-color: transparent;
	border: none;
	color: #7c7a7b;
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.8rem;
	letter-spacing: 0.1;
	text-align: left;
}

.site-header__nav__button-close::after {
	display: inline-block;
	height: 2.1rem;
	width: 2.1rem;
	content: "";
	background-image: url("../../img//svg/ecoflam__menu-close.svg");
	background-size: auto 100%;
	float: right;
}

.site-header__nav__overlay {
	display: none;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	background-color: rgba(0, 0, 0, 0.25);
	opacity: 0;
	transition: all .3s ease-out;
	z-index: 19;
}

.site-header__content {
	color: #fff;
	background-image: url("../../img//svg/ecoflam__background-title.svg");
	background-position: center;
	background-repeat: no-repeat;
}

.site-header__content__title {
	letter-spacing: 0.2em;
}

.site-header__content__title > span {
	display: block;
	font-family: "Cabin", sans-serif;
	font-size: 3rem;
	font-style: italic;
	letter-spacing: 0.05em;
}

.site-header__content__title > span:nth-child(1) {
	margin-bottom: 4rem;
	margin-top: 20rem;
}

.site-header__content__title > span:nth-child(2) {
	margin-top: 4rem;
	margin-bottom: 20rem;
}

/**
 * FOOTER
 */

.site-footer {
	background-color: #000;
}

.site-footer .site-section__title {
	margin-top: 7rem;
}

.site-footer .site-section__title::before {
	display: none;
}

.site-footer__title {
	margin: 0 0 5rem 0;
	color: #fff;
	font-size: 1.8rem;
	letter-spacing: 0.1em;
	text-transform: uppercase;
}

.site-footer__text {
	color: #7c7a7b;
	font-size: 1.6rem;
	line-height: 2.6rem;
}

.site-footer__map {
	margin: 2.6rem 0;
	height: 28rem;
	max-width: 100%;
}

.site-footer__copyright {
	margin-top: 7rem;
	border-top: 1px solid #363334;
}

.site-footer__copyright__text {
	margin: 3.5rem 0;
	color: #7c7a7b;
	font-size: 1.2rem;
}

/**
 * LAYOUT: PAGE
 */

.image--is-open .page-section__overlay-image {
	display: flex;
}

.page-section {
	position: relative;
}

.page-section__overlay-image {
	display: none;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	z-index: 10;
}

.page-section__overlay-image__img {
	max-width: 100%;
	height: auto;
}

.page-section__overlay-image__inner {
	position: relative;
	padding-bottom: 1rem;
	padding-top: 1rem;
	background-color: #fff;
}

.page-section__overlay-image__close {
	position: absolute;
	right: 0;
	top: 0;
	margin: 1rem;
	height: 2.5rem;
	width: 2.5rem;
	background-color: #c13b27;
	color: #fff;
	cursor: pointer;
	line-height: 2.9rem;
}

.page-section__overlay-image__close .svg {
	height: 2.5rem;
}

.page-section__navigation {
	padding-bottom: 6rem;
}

.page-section__navigation__link {
	display: inline-block;
	padding: 0.7rem 1rem;
	color: #363334;
	font-size: 1.5rem;
	text-transform: uppercase;
}

.page-section__navigation__link.current {
	background-color: #c13b27;
	border-radius: 4px;
	color: #fff;
}

.page-section__navigation__link.current::after {
	display: none;
}

.page-section::after {
	display: block;
	content: "";
	padding-bottom: 100%;
}

.page-section__inner {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	margin: 1rem;
	background-position: center;
	background-size: cover;
	cursor: pointer;
}

.page-section__text {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	margin: 2rem;
	z-index: 2;
}

.page-section__text__category {
	margin: 0;
	letter-spacing: 0;
	text-transform: uppercase;
	font-family: "NovecentoWide", sans-serif;
}

.page-section__text__title {
	margin: 0;
	font-style: italic;
}

.page-section__text__button {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: transparent;
	border: none;
	color: #fff;
	text-transform: uppercase;
}

.page-section__text__button::after {
	display: block;
	height: 2px;
	width: 0;
	position: absolute;
	bottom: -7px;
	left: auto;
	right: 0;
	background-color: #fff;
	content: "";
	transition: width .3s ease;
}

.page-section__overlay {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	background-color: #7c7a7b;
	opacity: 0.8;
	transition: 0.3s ease;
	z-index: 1;
}

.page-section:hover .page-section__overlay {
	background-color: #c13b27;
	transition: 0.3s ease;
}

.page-section:hover .page-section__text__button::after {
	width: 100%;
	left: 0;
	right: auto;
}

.page-section__contact-form {
	width: 100%;
}

.page-section__contact-form .wpcf7-form-control {
	display: block;
	height: 5rem;
	width: 100%;
	padding: 0 1rem;
	margin: 0.5rem 0;
	background-color: transparent;
	border: 1px solid #7c7a7b;
	border-radius: 4px;
	color: #7c7a7b;
	font-size: 1.6rem;
	font-family: "NovecentoWide", sans-serif;
	letter-spacing: 0.3em;
}

.page-section__contact-form .wpcf7-textarea {
	padding-top: 1rem;
	max-width: 100%;
	min-height: 25rem;
}

.page-section__contact-form .wpcf7-submit {
	width: 15rem;
	margin-bottom: 5rem;
	margin-left: auto;
	margin-right: auto;
	margin-top: 5rem;
	background-color: #c13b27;
	border-color: #c13b27;
	color: #fff;
	transition: 0.3s ease;
}

.page-section__contact-form .wpcf7-submit:hover {
	background-color: transparent;
	color: #c13b27;
	transition: 0.3s ease;
}

.wpcf7 .screen-reader-response,
.wpcf7-validation-errors,
.wpcf7-response-output {
	font-size: 1.6rem;
	font-family: "NovecentoWide", sans-serif;
	letter-spacing: 0.3em;
	line-height: 2.6em;
}

.wpcf7 .screen-reader-response > ul,
.wpcf7-validation-errors > ul,
.wpcf7-response-output > ul {
	display: none;
}

.wpcf7-response-output {
	color: #27c13b;
}

.wpcf7-mail-sent-ok {
	padding: 1rem;
	border: 1px solid #27c13b;
	border-radius: 4px;
}

.wpcf7-validation-errors {
	color: #c13b27;
}

.wpcf7-not-valid-tip {
	display: none;
}

.wpcf7-not-valid {
	border-color: #c13b27 !important;
}

.wpcf7-form .sent--is-ok {
	display: none;
}

.page-numbers {
	display: inline-block;
	height: 2.5rem;
	width: 2.3rem;
	border: 1px solid transparent;
	color: #363334;
	font-size: 1.2rem;
	line-height: 2.3rem;
	transition: 0.3s ease;
}

.page-numbers:hover {
	border: 1px solid currentColor;
	transition: 0.3s ease;
}

.page-numbers.current {
	background-color: #c13b27;
	border: none;
	color: #fff;
}

.page-numbers.next,
.page-numbers.prev {
	border: 1px solid currentColor;
}

.page-contact__footer {
	padding: 3rem 0;
	font-size: 1.8rem;
}

.page-contact__footer__title {
	color: #7c7a7b;
	font-family: "NovecentoWide", sans-serif;
	letter-spacing: 0.1em;
}

.page-contact__footer__text {
	color: #fff;
	font-style: italic;
}

/**
 *	DEFAULT EDITOR
 **/

img.alignnone {
	display: block;
	margin: 0 auto;
	max-width: 100%;
}

img {
	max-width: 100%;
	height: auto;
}

h4 {
	margin: 0;
	color: #363334;
}

p {
	margin-bottom: 2.6rem;
	margin-top: 0.6rem;
	color: #7c7a7b;
	font-size: 1.6rem;
	line-height: 2.6rem;
}

.alignleft,
img.alignleft {
	float: left;
}

.alignright,
img.alignright {
	float: right;
}

.alignnone,
img.alignnone {
	max-width: 100%;
	height: auto;
}

.wp-caption {
	margin: 0;
}

.wp-caption-text p {
	margin: 0;
}

@media (min-width: 481px) and (max-width: 768px) {

.hidden-at-s {
	display: none;
}

}

@media (min-width: 481px) {

.hidden-from-s {
	display: none;
}

.text-left-s {
	text-align: left;
}

.text-center-s {
	text-align: center;
}

}

@media (min-width: 769px) {

.hidden-from-m {
	display: none;
}

.text-left-m {
	text-align: left;
}

.text-center-s {
	text-align: center;
}

}

@media (min-width: 993px) {

.hidden-from-l {
	display: none;
}

}

@media (max-width: 992px) {

.hidden-to-m {
	display: none;
}

.menu-item {
	height: 5rem;
	width: 100%;
	line-height: 5rem;
	text-align: left;
}

.menu-item:hover {
	background-color: #7c7a7b;
}

.menu-item:hover > a {
	color: #fff;
}

.menu-item.current-menu-item {
	background-color: #7c7a7b;
}

.menu-item.current-menu-item > a {
	color: #fff;
	border-color: transparent;
	border-radius: 0px;
}

.menu-item > a {
	display: block;
	padding: 0 0 0 2.5rem;
	color: #363334;
	font-size: 1.4rem;
	letter-spacing: 0;
}

.menu-item > a:hover {
	border-color: transparent;
	border-radius: 0px;
}

.inner-wrapper__image {
	text-align: center;
}

.site-header__nav {
	position: fixed;
	bottom: 0;
	right: 0;
	top: 0;
	margin: 0;
	padding: 11.5rem 0 0 0;
	width: 24rem;
	background-color: #fff;
	transform: translate3d(100%, 0, 0);
	transition: all .3s ease-out;
	opacity: 0;
	z-index: 20;
}

.page-section__navigation__link {
	margin: 1rem 0;
}

}

@media (max-width: 768px) {

.hidden-to-s {
	display: none;
}

.menu-inline li {
	display: block;
}

.inner-wrapper {
	margin: 0 7.4rem;
}

.inner-wrapper .list-multiple-column {
	column-count: 2;
}

.site-section__text {
	margin-bottom: 7rem;
}

.site-section__text:first-child() {
	text-align: left;
}

.site-section.entreprise .entreprise__description {
	margin-bottom: 7rem;
}

.brands > a {
	margin-bottom: 7rem;
}

.event-nav {
	bottom: -3rem;
	left: 0;
	right: auto;
	top: auto;
}

}

@media (max-width: 480px) {

.hidden-at-xs {
	display: none;
}

html {
	font-size: 46.875%;
}

.inner-wrapper {
	margin: 0 2.5rem;
}

.inner-wrapper .list-multiple-column {
	margin: 7.4rem 0 0 0;
	column-gap: 7.4rem;
}

.inner-wrapper .text-multiple-column {
	column-count: 1;
}

.insert__title {
	font-size: 1rem;
}

.site-footer__title {
	margin-top: 7rem;
}

}

@keyframes slideFromLeft {

0% {
	transform: translate3d(-100%, 0, 0);
}

}

@keyframes slideFromRight {

0% {
	transform: translate3d(100%, 0, 0);
}

}

@keyframes slideToLeft {

99% {
	opacity: 1;
}

100% {
	opacity: 0;
	transform: translate3d(-100%, 0, 0);
}

}

@keyframes slideToRight {

99% {
	opacity: 1;
}

100% {
	opacity: 0;
	transform: translate3d(100%, 0, 0);
}

}

